import url from "../../common/config.js";
var _self;
export default {
	data() {
		return {
			flag: true,
			userList1: [],
			userList2: []
		}
	},
	onLoad() {
		_self = this;
		uni.request({
			url: url+'index.php/seek/trade',
			header: {
				"Access-Control-Allow-Origin": "*" //自定义请求头信息
			},
			success: res => {
				_self.userList1 = res.data[0];
				_self.userList2 = res.data[1];
			}
		});
	},
	methods: {
		change() {
			_self.flag = !_self.flag;
		},
		gotoUser() {
			uni.switchTab({
				url: "../find/find"
			})
		}
	}
};
